import { Center } from '@chakra-ui/react'
import React from 'react'

const Github = () => {
  return (
    <div id="github">
        <Center m="auto" data-aos="zoom-in" fontSize={{base:"25px",sm:"25x",md:"30px",lg:"35px"}} fontWeight="bold" borderBottom="solid 4px rgba(255, 115, 0, 0.801)" paddingBottom="0px" w={{base:"230px",sm:"230px",md:"280px",lg:"330px"}}>GITHUB CALENDER</Center>
    </div>
  )
}

export default Github