import { Box, Button, Flex, IconButton, Image, Menu, MenuButton, MenuItem, MenuList } from '@chakra-ui/react';
import React from 'react';
import img from "../../src/Images/img.png";
import "../Styles/Navbar.css";
import { DownloadIcon, HamburgerIcon } from '@chakra-ui/icons';
import { Link } from 'react-scroll';

const Navbar = () => {
  const [click,setClick] = React.useState(true);
  // const handleClick = () => {
  //   setClick(!click);
  // }
  const closeMenu = () => {
    setClick(true);
  }
  return (
    <Flex className="box_shadow" justifyContent="space-around" >
        <Box>
            <Image src={img} w={{base:"40%",sm:"40%",md:"35%",lg:"40%"}}/>
        </Box>
        <Flex className={click? "nav-menu active" : "nav-menu"} w={{md:"100%",lg:"65%"}} fontSize={{md:"16px",lg:"19px"}} fontWeight="bold" display={{base:"none", sm:"none", md:"flex", lg:"flex"}}>
            <Flex onClick={closeMenu} classname="nav-item"><Link to='about' spy={true} smooth={true} offset={-50} duration={500}>About Me</Link></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Link to='skills' spy={true} smooth={true} offset={-50} duration={500}>Skills</Link></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Link to='projects' spy={true} smooth={true} offset={-50} duration={500}>Projects</Link></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Link to='github' spy={true} smooth={true} offset={-50} duration={500}>GitHub Stats</Link></Flex>
            <Flex onClick={closeMenu} classname="nav-item"><Link to='contact' spy={true} smooth={true} offset={-50} duration={500}>Contact Me</Link></Flex>
            <a target="_blank" rel="noreferrer" href="https://drive.google.com/file/d/15o6A0qrMaHGVjzG8veE7v3y1DiYtCIvr/view?usp=share_link"><Button colorScheme='orange' size={{base:"sm",md:"sm",lg:"md"}}>Resume<DownloadIcon/></Button></a>
        </Flex>
            <Menu>
              <MenuButton
                  display={{base:"block", sm:"block", md:"none", lg:"none"}}
                  as={IconButton}
                  aria-label='Options'
                  icon={<HamburgerIcon />}
                  variant='outline'
              />
              <MenuList>
                  <MenuItem >
                  About Me
                  </MenuItem>
                  <MenuItem >
                  Skills
                  </MenuItem>
                  <MenuItem >
                  Projects
                  </MenuItem>
                  <MenuItem >
                  GitHub Stats
                  </MenuItem>
                  <MenuItem >
                  Contact Me
                  </MenuItem>
                  <MenuItem >
                  Download Resume <DownloadIcon/>
                  </MenuItem>
              </MenuList>
          </Menu>
    </Flex>
  )
}

export default Navbar